.profile-account {
    padding: 5px 10px;
    height: 33px;
    text-align: left;
    display: flex;
    align-items: center;

    img {
        width: 32px;
        height: 32px;
        border-radius: 5px;
    }
    span {
        padding-left: 10px;
        color: white;
        font-size: 13px;
        font-weight: lighter;
    }
    svg {
        padding-left: 5px;
    }
    path {
        color: white;
    }
}
